* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.js-start-race {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.js-winner, .js-progress {
  height: 50px;
  color: #000;
  margin: 10px 0;
  font-size: 18px;
}

.js-results-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.js-results-table th, .js-results-table td {
  text-align: left;
  border: 1px solid #ddd;
  padding: 8px;
}

.js-results-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

